.oni-pager .oni-pager-items {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    zoom: 1;
    *display: inline;
    vertical-align: middle;
}

.oni-pager .oni-pager-prev,
.oni-pager .oni-pager-next,
.oni-pager .oni-pager-item {
    display: -moz-inline-stack;
    display: inline-block;
    vertical-align: middle;
    *vertical-align: auto;
    zoom: 1;
    *display: inline;
    background-color: white;
    color: #2b76d1;
    height: 24px;
    line-height: 24px;
    margin-right: 5px;
    border: 1px solid #d4d4d4;
    padding: 0 10px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
    -ms-border-radius: 2px;
    -o-border-radius: 2px;
    border-radius: 2px;
    cursor: pointer;
    font-size: 12px;
    vertical-align: middle;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -moz-user-select: none;
    user-select: none;
    /*!当前页,没有边框*/
    /*!掠过*/
    /*!禁用*/
}

.oni-pager .oni-pager-prev.oni-state-active,
.oni-pager .oni-pager-next.oni-state-active,
.oni-pager .oni-pager-item.oni-state-active {
    color: #fff;
    border: 1px solid #2b76d1;
    padding: 1px 11px;
    cursor: default;
    background: #2b76d1;
}

.oni-pager .oni-pager-prev.oni-state-hover,
.oni-pager .oni-pager-next.oni-state-hover,
.oni-pager .oni-pager-item.oni-state-hover {
    border-color: 0 none;
}

.oni-pager .oni-pager-prev.oni-state-disabled,
.oni-pager .oni-pager-next.oni-state-disabled,
.oni-pager .oni-pager-item.oni-state-disabled {
    border-color: #d9d9d9;
    background-color: whitesmoke;
    color: #999999;
    cursor: default;
}

