html,
body {
    height: 100%;
}

.container_login {
    background: rgba(241, 241, 241, 0.64);
    font-family: "Microsoft yahei";
    font-size: 14px;
    background: url("../images/zhuce.jpg");
    height: 100%;
}

.login_page .top_b {
    font-family: "Microsoft yahei";
}

.login_page .login_box {
    top: 20%;
}

.ver-middle * {
    vertical-align: middle;
}

p {
    margin: 0;
    padding: 0;
}

#login_wrapper,
#register_wrapper {
    width: 100%;
    display: table;
    height: 100%;
    /*margin-top: -20px;*/
}

#login_content,
#register_content {
    display: table-cell;
    vertical-align: middle;
}

a {
    color: #177ee5;
}

#login_form {
    padding-bottom: 30px;
}

.login-btn {
    color: rgb(33, 33, 33);
    background-color: #177ee5;
    border: 0;
    width: 300px;
    height: 30px;
    line-height: 30px;
    color: #fff;
    display: inline-block;
    padding: 4px 14px;
    margin-bottom: 0;
    font-size: 14px;
    line-height: 20px;
    text-align: center;
    vertical-align: middle;
    cursor: pointer;
    font-family: "Microsoft yahei";
    border-radius: 5px;
    height: 45px;
}


/*报错*/

.u-error {
    text-align: left;
    font-size: 12px;
    color: #b94a48;
    height: 20px;
    line-height: 20px;
    padding-left: 20px;
    background: url("../images/error.png") no-repeat 0px 1px;
}

.error-input {
    color: #b94a48 !important;
    border-color: #b94a48 !important;
    background: #fff !important;
}

.error-input:focus {
    -webkit-box-shadow: 0 0 6px #d59392 !important;
    -moz-box-shadow: 0 0 6px #d59392 !important;
    box-shadow: 0 0 6px #d59392 !important;
    border-color: #953b39 !important;
}

.logon-text {
    height: 64px;
    line-height: 64px;
    font-size: 18px;
    color: #ffffff;
    font-family: "Microsoft yahei";
    margin: auto;
    width: 150px;
    position: relative;
    top: 15%;
}


/* login page */

.login_page {
    overflow: auto
}

.login_page body {
    height: 100%;
    max-width: inherit;
    margin: 0 20px
}

.login_page .login_box {
    position: relative;
    top: 50%;
    width: 380px;
    margin: 0 auto 24px;
    background: #fff;
    border: 1px solid #ccc;
    -webkit-border-radius: 6px;
    -moz-border-radius: 6px;
    -ms-border-radius: 6px;
    border-radius: 6px;
    -webkit-box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
    -moz-box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
    -ms-box-shadow: 0 0 6px rgba(0, 0, 0, 0.2);
    box-shadow: 0 0 6px rgba(0, 0, 0, 0.2)
}

.login_page .top_b {
    text-shadow: 0 1px 0 rgba(255, 255, 255, .5);
    font: 100 18px/42px 'PT Sans', sans-serif;
    height: 42px;
    padding: 0 20px;
    /* background: #e0e0e0; */
    /* border-bottom:1px solid #ccc; */
    -moz-border-radius-topleft: 6px;
    -moz-border-radius-topright: 6px;
    -moz-border-radius-bottomright: 0px;
    -moz-border-radius-bottomleft: 0px;
    -webkit-border-radius: 6px 6px 0px 0px;
    border-radius: 6px 6px 0px 0px;
    font-size: 15px;
    text-align: center;
    padding-top: 20px;
}

.login_page .cnt_b {
    padding: 30px 0 10px 0;
    width: 300px;
    margin: 0 auto
}

.login_page form {
    margin-bottom: 0
}

.login_page .btm_b {
    width: 300px;
    margin: 0 auto;
}

.login_page .links_b {
    width: 100%;
    font-size: 11px;
    text-align: center;
    position: absolute;
    bottom: -24px
}

.login_page .link_reg {
    font-size: 11px;
    padding: 5px 0 0;
    display: block
}

.alert-login {
    margin: 10px 10px 0;
    text-align: center;
    color: red;
    position: absolute;
    width: 100%;
    box-sizing: border-box;
    padding: 0;
}

input[type='text'],
input[type='password'] {
    border: none;
    height: 40px;
    line-height: 40px;
    text-indent: 5px;
    padding: 0;
    margin: 0;
    width: 255px;
}

.mt10 {
    margin-top: 10px;
    margin-bottom: 10px;
}

.mt15 {
    margin-top: 15px;
}

.go-register {
    text-align: right;
    margin: 0 auto 10px auto;
    width: 300px;
    font-size: 12px;
    color: #999999;
}

.go-register a {
    color: #0b97c4;
    text-decoration: underline;
}

.input-prepend {
    width: 300px;
    border: 1px solid #e0e0e0;
    height: 43px;
    line-height: 43px;
    border-radius: 5px;
    padding-top: 0;
    position: relative;
}

.input-prepend .stipIcon {
    position: absolute;
    top: 0;
    right: -20px;
}

.input-prepend i {
    color: #c0c0c0;
    font-size: 18px;
    width: 39px;
    border-right: 1px solid #c0c0c0;
    display: inline-block;
    height: 20px;
}

.add-on {
    display: inline-block;
    text-align: center;
    height: 36px;
    line-height: 36px;
}

.icon {
    background-image: url("../images/icon.png");
    background-repeat: no-repeat;
    width: 25px;
    height: 25px;
    display: inline-block;
}

.icon-cellphone-iphone {
    background-position: -17px -128px;
}

.icon-lock {
    background-position: -102px -128px;
}


