.select-warp {
    position: relative;
    float: left;
    border: 1px solid #dfdfe1;
    height: 28px;
    background: #f6f8fa url(img/select.png) no-repeat right 6px;
    width: 100%;
    /*text-indent: 2px;*/
    border-radius: 5px;
}

.select-warp:focus {
    /*background:url(img/select.png) no-repeat right -25px; border:1px solid #A71E32;*/
}

.select-warp .select-text {
    font-size: 12px;
    outline: none;
    border: 0 !important;
    background: none;
    width: 150px;
    border-radius: 3px;
    display: inline-block;
    overflow: hidden;
    height: 30px;
    line-height: 30px;
    padding: 0;
    vertical-align: middle;
    white-space: nowrap;
    color: #666;
    text-overflow: ellipsis;
    text-indent: 5px;
}

.select-btn {
    float: left;
    width: 20px;
    height: 23px;
    display: inline-block;
    margin-left: -1px;
}

.options-warp {
    border: 1px solid #C9C9C9;
    background: white;
    position: absolute;
    width: 200px;
    overflow: auto;
    max-height: 300px;
    top: 32px;
    left: -1px;
    z-index: 1;
    clear: both;
    display: none;
}

.options-warp ul {
    cursor: pointer;
    list-style: none;
    margin: 0;
    padding: 0;
}

.options-warp ul li {
    text-indent: 10px;
    overflow: hidden;
    line-height: 30px;
    height: 30px;
    list-style: none;
    width: 100%;
    color: #555;
    padding: 0;
}

.options-warp ul li.focus {
    background: #deedff;
    color: #000000;
}

.options-warp li .check-box {
    float: left;
    margin-left: 5px;
    margin-right: 0!important;
    margin-top: 5px!important;
}

.options-warp ul li .li-text {
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
    float: left;
    text-align: left;
}

.search-select-warp {
    background: none !important;
}

.search-select-btn {
    position: absolute;
    right: 5px;
    top: 4px;
    background: url(img/select.png) 0 0 #C9C9C9;
    width: 16px;
    height: 16px;
    display: inline-block;
}

.redMark {
    line-height: 24px;
}

.select-warp.disabled {
    background-color: #EEE;
    border: 1px solid #BBB;
}

.select-warp.work_nature {
    width: 250px;
    height: 40px;
    background: url(img/select.png) no-repeat right 9px;
}

.select-warp.work_nature .select-text {
    width: 250px !important;
    height: 40px;
    line-height: 40px;
}

.work_nature .options-warp {
    top: 41px;
}

.small-select.select-warp {
    height: 30px;
    line-height: 30px;
    background: #f6f8fa url(img/select.png) no-repeat right 7px;
}

.small-select.select-warp .select-text {
    line-height: 30px;
}

.small-select .options-warp {
    top: 32px;
}


