/*add:min 下拉框 高度30px*/

.select-warp-min {
    height: 30px;
    background: #f6f8fa url(img/select.png) no-repeat right 8px;
}

.select-warp-min .select-text {
    height: 30px;
    line-height: 30px;
}

.select-warp-min .options-warp {
    top: 30px;
}

.select-warp.s1 {
    background: none;
    border: 0;
    width: auto;
    background: url(img/select.png) no-repeat right 6px;
}

.select-warp.s1 .select-text {
